import Vue from 'vue'
import VueRouter from 'vue-router'
import Hello from '../src/components/Hello.vue'
import Login from '../src/components/Login.vue'
import App from './App.vue'
import StoriesPage from './components/StoriesPage.vue'
import StoriesAll from './components/StoriesAll.vue'
import StoriesFamous from './components/StoriesFamous.vue'

Vue.use(VueRouter)

const routes = [
    { 
        path: '/',
        name: 'home', 
        component: Hello 
    },
    {
        path: '/login',
        name: 'login',
        component: Login 
    },
    {
        path: '/stories',
        component: StoriesPage,
        children: [
        {
            path: '',
            name: 'stories.all',
            component: StoriesAll
        },
        {
            path: 'famous',
            name: 'stories.famous',
            component: StoriesFamous
        }
        ]
    }

]

const router = new VueRouter({
    mode: 'history',
    base: '/',
    routes // короткая форма для routes: routes
})

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    template: '<app></app>',
    components: {
        App
    }
})

